.site-header {
    background-image: url(./assets/Hero-image.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: auto;
}

.header-info {
    display: flex;
    justify-content: space-between;
    width: 80%;
    margin: 2em auto;
    align-items: center;

    .site-branding {
     /*   padding: 0 5em 0 0;
        margin: 0 0 0 10%;*/
        .site-title {
            margin: 0;
            a{
                font-size: 5vw;
                color: #ffffff;
                text-decoration: none;
                @media screen and (max-width: 605px){
                    font-size: 5vw;
                @media screen and (max-width: 460px){
                    font-size: 6vw;
                }
                }
            }
        }
    }

    .site-description {
        color: #ffffff;
        font-size: 1.5vw;
        text-transform: uppercase;

        @media screen and (max-width: 605px){
            font-size: 1.5vw;
        }
        @media screen and (max-width: 460px){
            font-size: 3vw;
        }
    }
    .header-btns {
        display: flex;
        justify-content: space-around;
        margin: 2em auto 0;

        .header-btn {
            color: #ffffff;
            font-size: 1.5vw;
            text-decoration: none;
            padding: 0.5em 1em;
            background-color: #000000;
            @media screen and (max-width: 460px){
                    font-size: 3vw;
            }
        }
        .header-btn:hover {
            background-color: #00cd00;
        }

        @media screen and (max-width: 590px){
            margin: 0;
        }
    }

    .header-photos {
        width: 100%;
        text-align: center;
        img {
            width: 14vw;
        }
        @media screen and (max-width: 460px){
            display: none;
        }
    }
    @media screen and (max-width: 525px){
        width: 90%;
    }
    @media screen and (max-width: 460px){
            margin-top: 0;
        }
}
