	width: 100%;
	height: auto;
}

.about-us .about-us-content {
	display: flex;
	justify-content: space-between;
	align-items: center;
	width: 90%;
	margin: 4em auto 0;
	padding: 4em 0;
}

.about-us .about-us-content .about-us-info {
	width: 40%;
	padding-right: 1em;
}

.about-us .about-us-content .about-us-info h3 {
	margin: 0;
}

.about-us .about-us-content .about-us-info p {
	font-size: 0.8em;
}

.about-us-services {
	display: flex;
	width: 60%;
	flex-wrap: wrap;
}

.about-us-services div h6 {
	font-size: 0.9em;
	margin: 0;
}

.about-us-services div p {
	margin: 0;
	font-size: 0.8em;
}

.service {
	display: flex;
	justify-content: space-around;
	align-items: center;
	width: 50%;
	margin-bottom: 2em;
}

.service h6 {
	font-size: 0.7em;
}

.service p {
	font-size: 0.6em;
}

.service .words {
	padding-left: 0.5em;
}

.team {
	background-color: #ebebeb;
}

.team h3 {
	margin-top: 0;
	padding-top: 2em;
}

.contact-us {
	width: 80%;
	margin: 0 auto;
}

.contact-us h3 {
	margin-bottom: 2vw;
}

.contact-info {
	display: flex;
	justify-content: space-between;
	align-items: flex-start;
}

.contact-info p {
	margin: 0;
}

@media screen and (max-width: 813px) {
	.contact-info {
		display: block;
	}
}

.ctas {
	width: 40%;
	padding-right: 3em;
}

@media screen and (max-width: 943px) {
	.ctas {
		padding-right: 1em;
	}
}

@media screen and (max-width: 813px) {
	.ctas {
		padding-right: 0;
		display: flex;
		width: 100%;
	}
}

.cta-1,
.cta-2,
.cta-3 {
	display: flex;
	margin-bottom: 3vw;
}

.cta-1 img,
.cta-2 img,
.cta-3 img {
	margin-right: 1em;
	width: 20%;
	height: 20%;
}

@media screen and (max-width: 813px) {
	.cta-1 img,
	.cta-2 img,
	.cta-3 img {
		justify-content: space-around;
		width: 5vw;
		height: 5vw;
		margin-right: 0;
	}
}

@media screen and (max-width: 520px) {
	.cta-1 img,
	.cta-2 img,
	.cta-3 img {
		width: 8vw;
		height: 8vw;
		text-align: center;
		margin: 0 auto;
	}
}

@media screen and (max-width: 813px) {
	.cta-1 {
		width: 40%;
	}
}

@media screen and (max-width: 813px) {
	.cta-2 {
		width: 30%;
	}
}

@media screen and (max-width: 813px) {
	.cta-3 {
		width: 30%;
	}
}

.cta-words h6 {
	margin: 0;
	font-size: 0.9em;
}

.cta-words p {
	margin: 0;
	font-size: 0.7em;
}

@media screen and (max-width: 520px) {
	.cta-words {
		display: none;
	}
}

.wpcf7-form p {
	margin: 0;
	padding-bottom: 0.5em;
}

.wpcf7-text {
	width: 25vw;
	background-color: #ebebeb;
}

@media screen and (max-width: 813px) {
	.wpcf7-text {
		width: 100%;
	}
}

.wpcf7-textarea {
	height: 10vw;
	width: 25vw;
	background-color: #ebebeb;
}

@media screen and (max-width: 813px) {
	.wpcf7-textarea {
		width: 100%;
		height: 20vw;
	}
}

.map {
	margin: 0 0 0 1em;
	height: 22vw;
	width: 40vw;
}

@media screen and (max-width: 813px) {
	.map {
		width: 100%;
		height: 20vw;
		margin: 2vw 0 0 0;
	}
}

/*--------------------------------------------------------------
## Comments
--------------------------------------------------------------*/
.comment-content a {
	word-wrap: break-word;
}

.bypostauthor {
	display: block;
}

/*--------------------------------------------------------------
## footer
--------------------------------------------------------------*/
.site-footer {
	background-color: #000000;
}

.site-footer .site-info {
	margin-top: 5.75vw;
	flex: 4 0 0;
}

.site-footer .site-info hr {
	width: 100%;
	margin-bottom: 0;
	background-color: #aaa9a9;
}

.footer-content {
	display: flex;
	justify-content: space-around;
	width: 80%;
	margin: 2em auto;
}

.footer-content img {
	height: 13vw;
}

.menu-foot-nav-container .menu,
.menu-social-media-container .menu {
	list-style-type: none;
	display: flex;
	justify-content: space-between;
	width: 70%;
	margin-left: auto;
	margin-bottom: 0.5em;
}

.menu-foot-nav-container .menu li a,
.menu-social-media-container .menu li a {
	font-size: 0.6em;
	text-decoration: none;
	color: #ffffff;
}

.menu-foot-nav-container .menu li a:hover,
.menu-social-media-container .menu li a:hover {
	color: #00cd00;
}

.menu-social-media-container .menu {
	width: 20%;
	margin-top: 0.5em;
}

.menu-social-media-container .menu .social-img {
	width: 2.5vw;
	height: 2.5vw;
}

.built-by {
	color: #ffffff;
	margin: 4em auto 2em;
	text-align: center;
	font-size: 0.5em;
}

.built-by a {
	text-decoration: none;
	color: #ffffff;
}

.built-by a:hover,
.built-by a:focus {
	text-decoration: underline;
	color: #00cd00;
}

@media screen and (max-width: 705px) {
	.site-footer .site-info {
		margin-top: 3.75vw;
	}
	.menu-foot-nav-container .menu {
		width: 90%;
	}
	.menu-social-media-container .menu {
		width: 30%;
	}
	.menu-social-media-container .menu .social-img {
		width: 3.5vw;
		height: 3.5vw;
	}
}

@media screen and (max-width: 525px) {
	.footer-content {
		display: block;
		width: 90%;
	}
}

@media screen and (max-width: 375px) {
	.menu-foot-nav-container .menu {
		width: 98%;
		justify-content: space-between;
		margin: 0.5em auto;
		padding-left: 0;
	}
	.menu-social-media-container .menu {
		width: 40%;
	}
	.menu-social-media-container .menu .social-img {
		width: 5.5vw;
		height: 5.5vw;
	}
	.built-by {
		width: 60%;
	}
}

/*--------------------------------------------------------------
# Infinite scroll
--------------------------------------------------------------*/
/* Globally hidden elements when Infinite Scroll is supported and in use. */
.infinite-scroll .posts-navigation,
.infinite-scroll.neverending .site-footer {
	/* Theme Footer (when set to scrolling) */
	display: none;
}

/* When Infinite Scroll has reached its end we need to re-display elements that were hidden (via .neverending) before. */
.infinity-end.neverending .site-footer {
	display: block;
}

/*--------------------------------------------------------------
# Media
--------------------------------------------------------------*/
.page-content .wp-smiley,
.entry-content .wp-smiley,
.comment-content .wp-smiley {
	border: none;
	margin-bottom: 0;
	margin-top: 0;
	padding: 0;
}

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
	max-width: 100%;
}

/* Make sure logo link wraps around logo image. */
.custom-logo-link {
	display: inline-block;
}

/*--------------------------------------------------------------
## Captions
--------------------------------------------------------------*/
.wp-caption {
	margin-bottom: 1.5em;
	max-width: 100%;
}

.wp-caption img[class*="wp-image-"] {
	display: block;
	margin-left: auto;
	margin-right: auto;
}

.wp-caption .wp-caption-text {
	margin: 0.8075em 0;
}

.wp-caption-text {
	text-align: center;
}

/*--------------------------------------------------------------
## Galleries
--------------------------------------------------------------*/
.gallery {
	margin-bottom: 1.5em;
}

.gallery-item {
	display: inline-block;
	text-align: center;
	vertical-align: top;
	width: 100%;
}

.gallery-columns-2 .gallery-item {
	max-width: 50%;
}

.gallery-columns-3 .gallery-item {
	max-width: 33.33%;
}

.gallery-columns-4 .gallery-item {
	max-width: 25%;
}

.gallery-columns-5 .gallery-item {
	max-width: 20%;
}

.gallery-columns-6 .gallery-item {
	max-width: 16.66%;
}

.gallery-columns-7 .gallery-item {
	max-width: 14.28%;
}

.gallery-columns-8 .gallery-item {
	max-width: 12.5%;
}

.gallery-columns-9 .gallery-item {
	max-width: 11.11%;
}

.gallery-caption {
	display: block;
}
